
import { defineComponent } from "vue";

export default defineComponent({
  name: "ModalTp",
  props: ["open", "tps"],
  emits: ["close", "selectTp"],
  data() {
    return {
      selectedStudent: null,
    };
  },
  methods: {
    close() {
      this.$emit("close");
    },
    selectTp(val: number) {
      this.$emit("selectTp", val);
      this.$emit("close");
    },
  },
});
